import React, { PureComponent } from 'react';
import styled, { css, cx } from 'react-emotion';
import { Link } from 'react-static';
import { Flex, Box } from 'grid-styled/emotion';
import SVGInline from 'react-svg-inline';
import HowItWorksAccordion from '../containers/HowItWorksAccordion';

import testTaker from '../images/winslow-test-taker.png';
import questionMark from '../images/twotone-contact_support-24px.svg';
import bgImage from '../images/hr_sectionbg.png';
import withGA from '../utils/withGA';
const h1Class = css`
  font-size: 25px;
  font-weight: 400;
  padding: 50px 20px;
  margin: 0;
`;

const h2Class = css`
  font-weight: 400;
  font-size: 2rem;
`;
const grey = css`
  background-color: rgb(243, 244, 244);
  text-align: center;
  img {
    margin-bottom: -130px;
  }
`;
const iconStyle = css`
  text-align: center;
  svg {
    fill: #db3a1b;
    height: 100px;
    width: 100px;
  }
`;

const redButton = css`
  background-color: #db3a1b;
  padding: 8px 15px;
  border-radius: 15px;
  color: white;
  font-weight: 500;
  text-decoration: none;
  box-shadow: 3px 3px 3px #ccc;
`;

const grayBox = css`
  background-image: url(${bgImage});
  background-repeat: repeat;
  background-position: center;
  text-align: justify;
  p {
    font-weight: 400;
  }
`;
class HowItWorks extends PureComponent {
  render() {
    return (
      <div>
        <Box className={grey} width={1}>
          <h1 className={h1Class}>
            Winslow personality testing for employment provide valuable insight into why some employees are functioning
            successfully and why others are performing unsatisfactorily.
          </h1>
        </Box>
        <Flex flexWrap="wrap" justifyContent="flex-start" alignItems="flex-start">
          <Box width={[1, 2 / 3]} px={4}>
            <p
              className={css`
                font-weight: 400;
                font-size: 14px;
                text-align: justify;
              `}
            >
              Pre-employment personality testing is one of, if not the most, important aspects of building and
              maintaining a successful organization. Research proves when a person fails in their career in more than
              90% of the time that failure is directly attributed to some aspect of human behavior. Individuals do not
              usually fail because of lack of education, experience, training, or skills. They fail because their
              personality is not compatible to the behavioral requirements of their career. An organization can invest
              significant amount of time and money creating products or services and a successful business. However, if
              the wrong applicants are hired it will fail or at best, never achieve its potential. The Winslow Applicant
              Selection Reports will significantly increase the probability that applicants you hire will be successful
              and achieve their maximum potential in your organization.
            </p>
          </Box>
          <Box width={[1, 1 / 3]}>
            <img src={testTaker} />
          </Box>
        </Flex>
        <Flex flexWrap="wrap">
          <Box px={4} width={[1, 1 / 4]}>
            <SVGInline className={iconStyle} svg={questionMark} component={'div'} />
          </Box>
          <Box px={[2, 5]} width={[1, 3 / 4]}>
            <h2>Questions the Pre-Employment Personality Reports Answer</h2>
            <p
              className={css`
                font-size: 14px;
                font-weight: 400;
                text-align: justify;
              `}
            >
              Our Pre-Employment Testing Reports prepared on the behavior and attitudes of applicants will predict their
              probability of success in their position. The Winslow Profile measures the 24 personality traits related
              to success and the Winslow Reports present the assessment results in an easy to understand format. The
              scores the applicant receives on the Winslow Traits will provide answers to the following primary
              questions employers want answered:
            </p>
          </Box>
        </Flex>
        <Box
          px={'40px'}
          pb={'20px'}
          className={css`
            font-size: 14px;
          `}
        >
          <HowItWorksAccordion />
        </Box>
        <Flex flexWrap="wrap" alignItems="center" justifyContent="space-around" className={grayBox}>
          <Box pl={[3, 6]} pr={3} width={[1, 2 / 3]}>
            <p>
              Our unique Pre-Employment Testing Applicant Selection Programs eliminate guesswork from the hiring
              process. Winslow Reports will assist you in selecting the best available applicant for every position in
              your organization, and reduce the risks and costs of hiring..
            </p>
          </Box>
          <Box p={2} width={[1, 1 / 3]} css={{ textAlign: 'center' }}>
            <Link to="/sign-up" className={redButton}>
              Start Now
            </Link>
          </Box>
        </Flex>
      </div>
    );
  }
}

export default withGA(HowItWorks);



