import React, { PureComponent } from 'react';
import { Flex, Box } from 'grid-styled/emotion';
import styled, { css } from 'react-emotion';
import withGA from '../utils/withGA';

const headline = css({
  backgroundColor: 'rgb(243, 244, 244)',
  padding: '20px',
  textAlign: 'center',
  h1: {
    fontSize: '1.5rem',
    fontWeight: 400
  }
});

class Testimonials extends PureComponent {
  render() {
    return (
      <Flex flexWrap="wrap">
        <Box p={1} width={1} className={headline}>
          <h1>What participants have to say about their Winslow Report . . .</h1>
        </Box>
        <Box p={3} width={[1, 1 / 2]}>
          <p>
            <b>Managing Partner, Major U.S. Accounting Firm</b>{' '}
            <i>"I will not make a Partner selection decision without seeing their Winslow Success Profile."</i>
          </p>
          <p>
            <b>T.B.</b>{' '}
            <i>
              After digesting my Winslow Profile, I know how fortunate I am to have stumbled upon this opportunity and
              chalk it up to destiny. Im glad I didnt follow several inclinations over the past seven years such as go
              back to college for a degree in psychology or counseling, as I was inclined to do. Instead I waited (not a
              strong suit for me) and it is now apparent to me that I have been waiting for this. Watching your training
              videos, I am inspired by the demeanor and complete confidence through which you communicate. You are
              answering questions and completing thoughts that have been hanging in thin air. Not just this, but I
              genuinely like the person I see on the film!
            </i>
          </p>
          <p>
            <b>Michael</b>{' '}
            <i>
              While I am handing out praise, and knowing I am the umpteenth person this week to tell you this, you and
              your remarkable program have earned our highest respect. I'll forever be grateful to Todd for introducing
              us to this evaluation tool. It's a stunningly great service and value! I've recommended The Winslow
              Profile to several friends. Hoping this finds you in good health and Spirits.{' '}
            </i>{' '}
          </p>
          <p>
            <b>Lee C. (Personal Coach):</b>
            <i>
              {' '}
              "Thank you, Bill Winslow, for your great contributions to us individually and as a coaching organization.
              Your assessment has favorably impacted lots and lots of lives just from my personal observation, including
              my own. Merry Christmas and Happy New Year to you and yours!"
            </i>
          </p>{' '}
          <p>
            <b>Arlette Sisnett:</b>{' '}
            <i>
              "The Winslow Programme helped me to improve my assertiveness and gave me greater self-confidence. The
              additional Winner's Edge training has left me with very good information which I can continue to use from
              day to day."
            </i>
          </p>
          <p>
            <b>Shawn E.</b>
            <i>
              {' '}
              A year ago I went to a peer group meeting, several of those members where using your services and spoke
              very highly of it, so I gave it a try. Although I believed them, I truly didnt buy into it. First, I gave
              it to my staff, was surprised on some things but most where right on. I had one younger manger that was
              not working out and we were going to end up doing something different with he scored very highly, so I
              called you and you said it was our company holding him back and we needed to work with him. I wanted to
              prove you wrong, so I followed your advice. After a year, he is a star manager for us just like you said
              he would be. Also, I was hiring two sales people and I had the list down to five people. Number five had a
              lot of knowledge and experience and I really liked her. Number one was very young and full of himself,
              again to prove you wrong I hired number one guy you said would be good, thinking he would fail. I hired
              number five person knowing she was going to kill it, even scoring much lower then you recommended to
              hiring. Well again, to my surprise the guy I would have never hired broke the record for most sales in a
              first year and my superstar number five person you told me not to hire could not sell hot chocolate to an
              Eskimo. There are other stories I could tell you, but I sure your head is big enough already! IM A
              BELEIVER!!!! Ive being in business over 28yrs in multiple companys this system works, and you have
              turned me into a believer and a strong supporter of your methods.{' '}
            </i>
          </p>{' '}
          <p>
            <b>Pamela Blackman:</b>
            <i>
              {' '}
              "Winslow is an objective assessment tool, and through the Personality Trait Groups, I have come to
              understand how these traits relate to each other, and to realize that one has the ability to change the
              weaker characteristics. It has highlighted those areas where I am both strong and weak. This provided the
              opportunity for me to conduct a self-examination and build my determination to develop those weaker
              areas."
            </i>
          </p>
        </Box>
        <Box p={3} width={[1, 1 / 2]}>
          <p>
            <b>Keith B</b>.{' '}
            <i>
              I happened to find an assessment (The Championship Profile) that I took February 1996! I'm amazed at how
              accurate it still describes me. Just wanted to say thank you for a great assessment!
            </i>
          </p>
          <p>
            {' '}
            <b>Vlad</b>
            <i>
              {' '}
              I have been exposed to the world of the Winslow Report since 2006 (where a coach recommended for me to
              take it) and then again in 2008-2009 when I worked with a winslow consultant and got some coaching session
              for me and explanation on my report. I actually was amazed how insightful the Winslow Profile was and I'd
              like to see what I can do for you if you need any help. I know a few startups that are in HR / Executive
              Search.
            </i>
          </p>
          <p>
            {' '}
            <b>M.K.</b>
            <i>
              {' '}
              "One word regarding the Overview of the WINSLOW DYNAMICS ASSESSMENT!!! WOW!! You nailed me...my
              strengths...my weaknesses (Ok Challenges)...my need to be part of a team...all my sensitivities and my
              insecurities...wow!! I mean WOW!! Thank you so much...much of this I have learned, unfortunately the hard
              way. But you putting it in one document is very comforting. Your ability to point out the positive and the
              Alerts on how to keep things in control is very helpful and I will use as my coach moving forward in all
              my endeavors. I will keep this document close and review often to keep myself in check."
            </i>
          </p>
          <p>
            {' '}
            <b>Nigel Harris</b>:{' '}
            <i>
              "The Winslow experience has strengthened my faith in my ability to achieve - to free myself from the
              shackles of limi tations. My philosophy is: what someone thinks about me does not define who I am."
            </i>
          </p>
          <p>
            <b>Rodney Doffin: </b>
            <i>
              "The Winslow Programme helped me to identify those traits that are most likely to restrain me from
              reaching my goals and those that are most likely to help me achieve them. It provided a mechanism for me
              to recognize opportunities for im provement and guided me through the stages of personal change and
              development. I have grown as a result of the Winslow Assessment Profile and the coaching sessions. That
              growth is reflected in my personal and work relationships, (so my friends and colleagues tell me). I feel
              a sense of achievement as I reflect on the progress I have made. My goal is to be 'Unconsciously
              Competent' (permanent personality change - high performance by reflex)."
            </i>
          </p>
          <p>
            <b>Hal Hunte:</b>{' '}
            <i>
              "As a Winslow participant I have learned about the personality traits which I have that can assist me in
              working to my personal potential in my job. I have also learned about those that could hold me back from
              achieving my goals. As a coach I have learned How to communicate more effectively and have acquired those
              skills which could assist me in motivating my proteges to set and achieve realistic goals for themselves."{' '}
            </i>
          </p>{' '}
          <p>
            <b>Peter Williams:</b>
            <i>
              {' '}
              "It is very difficult to look at yourself objectively and see your own faults. Seeing faults in others is
              by far the easier task, Through Winslow I have been able to look at myself with an objective eye. I admit
              that I was a little anxious about what it would say. I am working on those aspects of my personality which
              need improving and, while I still have my faults, I have changed and will continue to change and be able
              to contribute more to my family, my friends and to this Company."
            </i>
          </p>
        </Box>
      </Flex>
    );
  }
}

export default withGA(Testimonials);



