import React, { PureComponent } from 'react';
import { Flex, Box } from 'grid-styled/emotion';
import styled, { css } from 'react-emotion';
import phoneIcon from '../images/outline-phone-24px.svg';
import emailIcon from '../images/outline-email-24px.svg';
import SVGInline from 'react-svg-inline';

const StyledBox = styled(Box)`
  background-color: white;
  font-size: 0.8em;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color: rgba(0, 0, 0, 0.87);

  a {
    color: #db3a1b;
    vertical-align: top;
    text-decoration: none;
    padding-top: 10px;
    padding-left: 10px;
  }

  @media (min-width: 800px) {
    background-color: #003468;
    color: rgba(255, 255, 255, 0.5);

    a {
      color: white;
      padding-left: 8px;
    }
  }
`;

const HideMobile = styled(Box)`
  @media (max-width: 800px) {
    display: none;
  }
`;
const iconStyle = css`
  svg {
    fill: white;
    height: 17px;
    width: 17px;
  }
  @media (max-width: 800px) {
    svg {
      fill: #db3a1b;
    }
  }
`;

class Header extends PureComponent {
  render() {
    const { className } = this.props;
    return (
      // <Box>foo</Box>
      <StyledBox p={3} is="header" className={className}>
        <Flex justifyContent="space-evenly" flexWrap="wrap">
          <HideMobile>Empowering You to Hire and Develop the Best!</HideMobile>
          <Box>
            <SVGInline className={iconStyle} svg={phoneIcon} />
            {'  '}
            <a href="tel:512-775-4260">512-775-4250</a>
          </Box>
          <Box>
            <SVGInline svg={emailIcon} className={iconStyle} />
            {'  '}
            <a href="mailto:info@winsailconsulting.com">info@winsailconsulting.com</a>
          </Box>
        </Flex>
      </StyledBox>
    );
  }
}

export default Header;



