import React, { PureComponent } from 'react';
import styled, { css, cx } from 'react-emotion';
import { Flex, Box } from 'grid-styled/emotion';
import { Link } from 'react-static';
import FormInput from '../containers/formInput';
import FormTextArea from '../containers/formTextArea';
import Recaptcha from 'react-recaptcha';
import axios from 'axios';
import qs from 'qs';
import Spinner from '../containers/Spinner';
import withGA from '../utils/withGA';

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

const h1Class = css`
  font-size: 2.5rem;
  font-weight: 300;
  padding: 50px 20px 0px 20px;
  margin: 0;
`;

const h2Class = css`
  font-weight: 400;
  font-size: 1.8rem;
  text-align: center;
`;

const RedButton = styled('button')(props => ({
  backgroundColor: props.disabled === 'disabled' ? 'gray' : '#db3a1b',
  padding: '8px 15px',
  borderRadius: '15px',
  color: 'white',
  fontWeight: 500,
  textDecoration: 'none',
  boxShadow: '3px 3px 3px #ccc'
}));

const formButtons = css`
  margin-bottom: 50px;
  @media (max-width: 800px) {
    div {
      padding-bottom: 30px;
    }
  }
`;
let recaptchaInstance;

// create a reset function
const resetRecaptcha = () => {
  recaptchaInstance.reset();
};

class SignUp extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      values: { email: '', name: '', subject: '', form_tools_form_id: 1 },
      errors: null,
      isDirty: false,
      isSubmitting: false,
      submitted: false,
      isValid: false,
      emailValid: false,
      recaptcha: false,
      loading: false
    };
    // this.handleBlur = this.handleBlur.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleReset = this.handleReset.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.verifyCallback = this.verifyCallback.bind(this);
  }

  handleSubmit = event => {
    event.preventDefault();
    // const self = this;
    this.setState({ loading: true });
    axios
      .post('https://lynnkindlercoaching.com/ftools/process.php', qs.stringify(this.state.values))
      .then(res => {
        this.setState({ submitted: true, loading: false });
        setTimeout(() => this.handleReset(), 5000);
      })
      .catch(error => {
        console.log('AXIOS ERROR:', error);
        this.setState({ submitted: true, loading: false });
        setTimeout(() => this.handleReset(), 5000);
      });
  };

  verifyCallback = response => {
    // console.log('recaptcha', response);
    this.setState({ recaptcha: true });
    this.validate();
  };

  handleChange = e => {
    const key = e.target.id;
    const value = e.target.value;
    if (key === 'email') {
      const testValue = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value);
      this.setState({
        emailValid: testValue
      });
    }
    this.setState({
      values: {
        ...this.state.values,
        [key]: value
      }
    });
    // console.log(this.state);
    this.validate();
  };

  validate = () => {
    const { values, emailValid, recaptcha } = this.state;
    if (emailValid && values.name.length > 1 && values.subject.length > 1 && recaptcha) {
      this.setState({ isSubmitting: true });
    } else {
      this.setState({ isSubmitting: false });
    }
  };

  handleReset = () => {
    resetRecaptcha();
    this.setState({
      values: { email: '', name: '', subject: '', form_tools_form_id: 1 },
      isSubmitting: false,
      isValid: false,
      emailValid: false,
      recaptcha: false,
      submitted: false,
      loading: false
    });
  };

  render() {
    const { email, name, subject } = this.state.values;
    // const { errors, submitted } = this.state;
    const { isDirty, isSubmitting, isValid, emailValid, submitted, loading } = this.state;

    return (
      <div>
        <h1 className={h1Class}>Find out more about Winslow Reports</h1>
        <h2 className={h2Class}>Schedule a demo now.</h2>

        <form onSubmit={this.handleSubmit}>
          <input type="hidden" name="form_tools_form_id" value="2" />
          <Flex flexDirection="column">
            <FormInput label="name" valid={name.length > 1} handleChange={this.handleChange} value={name} />
            <FormInput label="email" valid={emailValid} handleChange={this.handleChange} value={email} />
            <FormTextArea label="subject" valid={subject.length > 1} handleChange={this.handleChange} value={subject} />
          </Flex>
          {submitted ? <DisplayThankYouMessage /> : null}
          {loading ? <SubmittedMessage /> : null}
          <Flex flexWrap="wrap" alignItems="center" justifyContent="space-around" className={formButtons}>
            <Box>
              <RedButton
                onClick={() => {
                  this.handleReset();
                }}
              >
                Reset
              </RedButton>
            </Box>
            <Box>
              <RedButton type="submit" disabled={isSubmitting === true ? null : 'disabled'}>
                Submit
              </RedButton>
            </Box>
            <Box>
              <Recaptcha
                sitekey="6LcbiBgUAAAAAGDFg5mVa7aB9zF1t-kldKal0Vj5"
                render="explicit"
                verifyCallback={this.verifyCallback}
                ref={e => (recaptchaInstance = e)}
              />
            </Box>
          </Flex>
        </form>
      </div>
    );
  }
}

export default withGA(SignUp);

const SubmittedMessage = props => {
  return (
    <Box
      className={css`
        text-align: center;
      `}
    >
      <Spinner />
    </Box>
  );
};
const DisplayThankYouMessage = props => {
  return (
    <Box
      className={css`
        text-align: center;
      `}
    >
      <p
        className={css`
          font-size: 20px;
          font-weight: 700;
        `}
      >
        Thank you for contacting us. Someone will respond to your request soon.
      </p>
    </Box>
  );
};



