import React, { Component } from 'react';
import Accordion from './Accordion';
import AccordionItem from './AccordionItem';
import AccordionHeader from './AccordionHeader';

export default class RunAccordion extends Component {
  render() {
    return (
      <div>
        <Accordion>
          <AccordionItem name="a" caption="Is this applicant a success orientated individual?">
            <ul>
              <li>
                <strong>Ambition:</strong> this trait score tells you if this person is a competitive, goal oriented
                person who has a strong desire to successful.
              </li>
              <li>
                <strong>Self-confidence:</strong> tells you if the applicant has supreme confidence in him or herself
                and truly believes they have what it takes to be successful.
              </li>
              <li>
                <strong>Conscientiousness:</strong> tells you if this person places the welfare of management and the
                organization before their own personal self-interest.
              </li>
            </ul>
          </AccordionItem>

          <AccordionItem name="b" caption="Will this person be a dedicated and cooperative employee?">
            <ul>
              <li>
                <strong>Coachability:</strong> will tell you if this person respects authority figures (their managers)
                and willingly accepts their leadership and direction.
              </li>
              <li>
                <strong>Recognition:</strong> indicates if this person has an internal motivation to be seen as a
                desirable person and will act appropriately to receive recognition.
              </li>
              <li>
                <strong>Trust:</strong> trusting individuals are not suspicious and defensive. They openly communicate
                and believe others are deserving of their trusting nature.
              </li>
              <li>
                <strong>Flexibility:</strong> tells you if this applicant will readily adapt to the company's methods of
                operation and decisions or be resistant and insist on their own.
              </li>
              <li>
                <strong>Contentment:</strong> lets you know if this is a happy person with a positive disposition,
                rather than someone who is disenchanted with their life.
              </li>
              <li>
                <strong>Responsibility:</strong> will this individual accept responsibility for the consequences of his
                or her words and actions or blame others instead.
              </li>
            </ul>
          </AccordionItem>
          <AccordionItem name="c" caption="What is this individual's interpersonal orientation?">
            <ul>
              <li>
                <strong>Leadership:</strong> tells you if this individual believes they are a leader and if they enjoy
                managing, motivating and being responsible for others.
              </li>
              <li>
                <strong>Sociability:</strong> discloses whether the applicant is a people-oriented extrovert or an
                introvert who focuses on "things" and avoids contact with others.
              </li>
              <li>
                <strong>Exhibition:</strong> reveals if this person enjoys being the center of attention, someone who is
                entertaining, demonstrative and a pleasure to be with.
              </li>
              <li>
                <strong>Nurturance:</strong> nurturing individuals are keenly aware of and sensitive to the emotional
                needs of others and readily respond with sympathy and support.
              </li>
            </ul>
          </AccordionItem>

          <AccordionItem name="d" caption="Does this applicant meet the intellectual requirements for an employee?">
            <ul>
              <li>
                <b>Alertness:</b> measures the applicant's inherent ability to learn quickly, understand complex
                situations and successfully solve problems.
              </li>
              <li>
                <b>Structure:</b> indicates how organized their thinking, planning and actions will be. They will be
                highly mentally structured and disciplined.
              </li>
              <li>
                <b>Order:</b> tells you if they will keep their physical surroundings, neat and orderly. They will have
                a place for everything and everything in its place.
              </li>
              <li>
                <b>Control:</b> will determine if they are impulsive and talk or act without thinking, or someone who
                will control their impulsive behavior.
              </li>
            </ul>
          </AccordionItem>

          <AccordionItem name="e" caption="Does this applicant have the required emotional maturity and discipline?">
            <ul>
              <li>
                <b>Sociability:</b> will determine if they enjoy interacting with others and will be perceived as a warm
                and friendly person others enjoy being with.
              </li>
              <li>
                <b>Endurance:</b> tells you if they have the inherent physical energy and persistence required to
                prospect, make presentations and close sales.
              </li>
              <li>
                <b>Assertiveness:</b> enables individuals to persuade others to do what they want and to accept their
                recommendations; they make things happen.
              </li>
              <li>
                <b>Tough-mindedness:</b> equips those who must sell to accept the rejection, disappointments and
                setbacks that are inevitable in sales situations.
              </li>
            </ul>
          </AccordionItem>
          <AccordionItem name="f" caption="Does this applicant have an inherent sales personality?">
            <ul>
              <li>
                <b>Sociability:</b> will determine if they enjoy interacting with others and will be perceived as a warm
                and friendly person others enjoy being with.
              </li>
              <li>
                <b>Endurance:</b> tells you if they have the inherent physical energy and persistence required to
                prospect, make presentations and close sales.
              </li>
              <li>
                <b>Assertiveness:</b> enables individuals to persuade others to do what they want and to accept their
                recommendations; they make things happen.
              </li>
              <li>
                <b>Tough-mindedness: </b>
                equips those who must sell to accept the rejection, disappointments and setbacks that are inevitable in
                sales situations.
              </li>
            </ul>
          </AccordionItem>
        </Accordion>
      </div>
    );
  }
}



