import React, { PureComponent } from 'react';
import logoImg from '../images/logo.png';
import { Box, Flex } from 'grid-styled/emotion';
import winslowLogo from '../images/Logo-WinslowCertifiedConsultant.jpg';
import styled, { css } from 'react-emotion';

const CopyRight = styled('div')(props => ({
  margin: '0 auto',
  fontSize: '.7em',
  paddingBottom: '10px'
}));

class Footer extends PureComponent {
  render() {
    const year = new Date().getFullYear();
    return (
      <Flex
        flexWrap="wrap"
        className={css({
          borderTop: '5px solid #e4edee'
        })}
      >
        <Box p={3} width={[1, 1 / 2]}>
          <img src={logoImg} />
        </Box>
        <Box p={3} width={[1, 1 / 2]}>
          <img src={winslowLogo} />
        </Box>
        <CopyRight>&copy; {year} Winsail Consulting. All Rights Reserved.</CopyRight>
      </Flex>
    );
  }
}

export default Footer;

