import React, { PureComponent } from 'react';
import styled, { css, cx } from 'react-emotion';
import { Link } from 'react-static';
import { Flex, Box } from 'grid-styled/emotion';
import penImg from '../images/sign-up-pen.png';
import how1 from '../images/winslow_how_it_works_1.png';
import how2 from '../images/winslow_how_it_works_2.png';
import how3 from '../images/winslow_how_it_works_3.png';
import withGA from '../utils/withGA';

const h1Class = css`
  font-size: 25px;
  font-weight: 400;
  padding: 50px 20px;
  margin: 0;
`;

const h2Class = css`
  font-weight: 400;
  font-size: 2rem;
`;
const grey = css`
  background-color: rgb(243, 244, 244);
  text-align: center;
  img {
    margin-bottom: -130px;
  }
`;

const forYouContainer = css`
  padding-left: 40px;
  padding-right: 50px;
  padding-bottom: 30px;
  @media (max-width: 800px) {
    padding-right: 40px;
    div {
      text-align: center;
    }
  }
  img {
    float: right;
    padding-right: 20px;
    @media (max-width: 800px) {
      padding-right: 0px;
      float: none;
      text-align: center;
    }
  }
  h2 {
    margin: 0;
    text-align: center;
    padding-right: 50px;
    @media (max-width: 800px) {
      padding-right: 0px;
    }
  }
  p {
    font-size: 14px;
    padding-right: 50px;
    @media (max-width: 800px) {
      padding-right: 0px;
      text-align: justify;
    }
  }
`;

const for1 = css`
  padding-top: 100px;
`;

const redButton = css`
  background-color: #db3a1b;
  padding: 8px 15px;
  border-radius: 15px;
  color: white;
  font-weight: 500;
  text-decoration: none;
  box-shadow: 3px 3px 3px #ccc;
`;

const spacedDiv1 = css`
  margin-left: 200px;
  padding-right: 10px;
  @media (max-width: 800px) {
    margin-left: 0px;
  }
`;

class ForYou extends PureComponent {
  render() {
    return (
      <div>
        <Box className={grey} width={1}>
          <h1 className={h1Class}>
            Capitalize on your personality assets, control your behavior, and modify your personality.
          </h1>
        </Box>
        <Box className={grey} width={1}>
          <img src={penImg} />
        </Box>
        <Flex flexWrap="wrap" justifyContent="flex-start" className={cx(for1, forYouContainer)} alignItems="flex-start">
          <Box width={[1, 1, 1 / 3]}>
            <img src={how1} />
          </Box>
          <Box width={[1, 1, 3 / 5]}>
            <h2 className={h2Class}> The Job Market is Tough</h2>
            <p>
              Why are some people still stuck in a job which provides few rewards? Why are some still looking for
              employment? Interviewers are looking for anything that gives one candidate an edge over another. They
              cant afford to make mistakes, and neither can you. Thats frustrating when you have a lot to offer and no
              one seems to notice. You need an edge, something that differentiates and strengthens you, and you need it
              NOW!
            </p>
          </Box>
        </Flex>

        <Flex flexWrap="wrap" alignItems="flex-start" className={cx(forYouContainer, spacedDiv1)}>
          <Box width={[1, 1, 1 / 3]}>
            {' '}
            <img src={how2} />
          </Box>
          <Box width={[1, 1, 3 / 5]}>
            <h2 className={h2Class}>What the Winslow Report Can Do For You</h2>
            <p>
              Your Winslow Report is prepared from the results of the Winslow Dynamics Profile, a validated personality
              assessment instrument that measures personality traits. This comprehensive Profile measures the 24
              personality characteristics related to career success and personal contentment. The Profile is Internet
              based, user-friendly, and enables you to complete the Profile at your location and convenience.
            </p>
          </Box>
        </Flex>
        <Flex flexWrap="wrap" alignItems="flex-start" className={forYouContainer}>
          <Box width={[1, 1, 1 / 3]}>
            {' '}
            <img src={how3} />
          </Box>
          <Box width={[1, 1, 3 / 5]}>
            <h2 className={h2Class}>The profile used by professional sports teams</h2>
            <p>
              We all realize that multi-million dollar contracts to play sports are handed out to the best athletes
              available. Skill on the field is essential. But, did you know that psychological factors are heavily
              considered when draft choices are made? Winslow Research has profiled tens of thousands of players and
              draftees for Major League Baseball, the NBA, and selected teams in the NFL and Pro Hockey as well as
              dozens of colleges and high schools. Results of these specialized Profiles are taken very seriously when
              offering scholarships or contracts to prospective athletes.
            </p>
          </Box>
        </Flex>
        <Flex flexWrap="wrap" alignItems="flex-start" justifyContent="space-around">
          <Box width={[1, 1, 2 / 3]}>
            <p
              className={css`
                font-size: 14px;
                font-weight: 400;
                padding: 0 100px;
                @media (max-width: 800px) {
                  padding: 0px 40px;
                }
              `}
            >
              The Profile takes less than one hour to complete, and your results are available on the Internet shortly
              after the Profile is complete. From the results of your Profile a 45 to 55 page Report will be created
              that presents objective information on your personality, behavior and attitudes. With this information,
              you can capitalize on your personality assets, modify your personality, and control your behavior. The
              results will help you achieve success in your career and contentment in your personal lifestyle. In
              addition to personal development, your Report can be used for pre-marital preparation, enhance marriage
              and family relations, and other applications.
            </p>
          </Box>
          <Box px={2} width={[1, 1, 1 / 3]}>
            <div
              className={css`
                margin-top: 45px;
                @media (max-width: 800px) {
                  margin-top: 10px;
                  text-align: center;
                  margin-bottom: 30px;
                }
              `}
            >
              <Link to="/sign-up" className={redButton}>
                Start Now
              </Link>
            </div>
          </Box>
        </Flex>
      </div>
    );
  }
}

export default withGA(ForYou);



