import React from 'react';
import { Router, Link, Loading, withLoading } from 'react-static';
import styled, { css, injectGlobal } from 'react-emotion';
import { Flex, Box } from 'grid-styled/emotion';
import Header from './containers/Header';
import Footer from './containers/Footer';
import Routes from 'react-static-routes';
import LoadMenu from './containers/Menu';
import ScrollUpButton from 'react-scroll-up-button';

import 'typeface-montserrat';

injectGlobal`
* {
  box-sizing: border-box;
}

  body {
    font-family: "Montserrat", Helvetica, Arial, sans-serif;
    font-size: 16px;
    margin: 0;
    padding: 0;
    color: #272727;
    background-color: #e4edee;
    padding: 30px 20px;
  }

  h1 {font-size: 3rem; font-weight: 300; padding:20px 0; text-align:center; color: #003468;}
`;

const StyledContainer = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  border-radius: 10px;
  background-color: white;
`;

const App = () => (
  <Router>
    <Loading>
      {({ loading }) => {
        if (loading) {
          return (
            <div>
              <span>Loading...</span>
            </div>
          );
        } else {
          return (
            <StyledContainer>
              <Box>
                <Box m="auto">
                  <Header />
                  <LoadMenu />
                </Box>
                <Box>
                  <Routes />
                </Box>
                <ScrollUpButton />
                <Footer />
              </Box>
            </StyledContainer>
          );
        }
      }}
    </Loading>
  </Router>
);

export default App;



