import React, { Children, Component, cloneElement } from 'react';
import AccordionHeader from '../containers/AccordionHeader';
import AccordionItem from '../containers/AccordionItem';

class Accordion extends Component {
  constructor(props) {
    super(props);
    let state = { openedHash: {} };

    Children.toArray(props.children).forEach(child => {
      if (child) {
        state.openedHash[child.props.name] = { opened: !!child.props.defaultOpen, checked: !!child.props.defaultCheck };
      }
    });

    this.state = state;
  }
  onToggle = name => {
    let openedHash = this.state.openedHash;
    this.setState({
      openedHash: { ...openedHash, [name]: { opened: !openedHash[name].opened, checked: openedHash[name].checked } }
    });
  };
  onCheck = name => {
    let openedHash = this.state.openedHash;
    this.setState({
      openedHash: { ...openedHash, [name]: { checked: !openedHash[name].checked, opened: openedHash[name].opened } }
    });
  };
  render() {
    let { openedHash } = this.state;
    let { children } = this.props;
    return (
      <div>
        {Children.toArray(children)
          .filter(c => c)
          .map(child =>
            cloneElement(child, {
              key: child.props.name,
              expanded: openedHash[child.props.name].opened,
              checked: openedHash[child.props.name].checked,
              onToggle: this.onToggle
            })
          )}
      </div>
    );
  }
}

export default Accordion;



