import React, { PureComponent } from 'react';
import styled, { css, cx } from 'react-emotion';
import { Flex, Box } from 'grid-styled/emotion';
import { Link } from 'react-static';

import SVGInline from 'react-svg-inline';
import bgImage from '../images/hr_sectionbg.png';
import checkIcon from '../images/baseline-check_circle_outline-24px.svg';
import withGA from '../utils/withGA';

const h1Class = css`
  font-size: 2rem;
  font-weight: 400;
  padding: 50px 20px;
  margin: 0;
`;

const h2Class = css`
  font-weight: 400;
  font-size: 2rem;
  text-align: center;
`;
const grey = css`
  background-color: rgb(243, 244, 244);
  text-align: center;
  img {
    margin-bottom: -130px;
  }
`;
const iconStyle = css`
  text-align: center;
  svg {
    fill: #db3a1b;
    height: 100px;
    width: 100px;
  }
`;

const redButton = css`
  background-color: #db3a1b;
  padding: 8px 15px;
  border-radius: 15px;
  color: white;
  font-weight: 500;
  text-decoration: none;
  box-shadow: 3px 3px 3px #ccc;
`;

const grayBox = css`
  background-image: url(${bgImage});
  background-repeat: repeat;
  background-position: center;
  text-align: justify;
  p {
    font-weight: 400;
  }
`;

const checkStyle = css`
  svg {
    fill: rgba(0, 0, 0, 0.54);
    height: 20px;
    width: 20px;
    align-self: flex-start;
    flex-shrink: 0
    margin-right: 0px;
  }
`;

const checkPanel = css`
  background-color: #f5f8f8;

  ul {
    list-style: none;
    padding: 0px;
  }
  li {
    display: flex;
    position: relative;
    text-align: left;
    justify-content: flex-start;
    padding: 10px 20px;
  }

  ul li div {
    flex: 1 1 auto;
    padding-left: 20px;
  }
`;

const traitsStyles = css`
  h3 {
    font-size: 1.5rem;
    font-weight: 300;
    padding: 0;
    margin: 0;
    text-align: center;
  }

  p {
    font-size: 14px;
    text-align: justify;
  }
`;

const centerBorder = css`
  border-bottom: 1px solid #db3a1b;
  text-align: center;
  margin-bottom: 20px;
`;
const redBox = css`
  width: 30px;
  height: 30px;
  background-color: #db3a1b;
  display: block;
  color: white;
  line-height: 30px;
  margin: 0 auto;
  margin-bottom: -15px;
`;

class WinslowInternetAssessment extends PureComponent {
  render() {
    return (
      <div>
        <Box className={grey} width={1}>
          <h1 className={h1Class}>The Winslow Internet Assessment System</h1>
        </Box>
        <Box>
          <p
            className={css`
              text-align: justify;
              padding: 20px 50px;
              line-height: 1.5rem;
            `}
          >
            The <i>Winslow Pre-Employment Testing Program</i> is an Internet-based system to measure the personality,
            behavior and attitudes of applicants for positions and provide feedback to organizations on the applicant's
            probability of success in a specific position. The user-friendly system enables organizations to administer
            their assessment activity and have total control over the process. Winslow Research will train one or
            preferably two employees of the organization to be the Winslow Reports Administrator. In just a few hours,
            these individuals will be competent to serve as the Internet Webmaster for the organization. Following are
            the procedures, features, functions and components of the System.
          </p>
        </Box>
        <Box mx={'10%'} className={checkPanel}>
          <ul>
            <li>
              {' '}
              <SVGInline className={checkStyle} svg={checkIcon} />
              <div>
                <b>Private Winslow Website:</b> at no fee to your organization, a secure Internet Website will be
                created to manage your assessment activity.
              </div>
            </li>
            <li>
              <SVGInline className={checkStyle} svg={checkIcon} />
              <div>
                {' '}
                <strong>Position Analysis:</strong> the first step is a formal or informal position analysis conducted
                by Winslow Research, at no fee to the client, to determine the behavioral requirements for each of your
                applicants positions.
              </div>
            </li>
            <li>
              <SVGInline className={checkStyle} svg={checkIcon} />
              <div>
                {' '}
                <strong>The Assessment System and Process:</strong> the applicant selection assessment process is a
                user-friendly four step procedure.
              </div>
            </li>
          </ul>
        </Box>
        <Flex flexWrap="wrap" className={traitsStyles} justifyContent="space-around">
          <Box width={[1, 1, 1 / 4]} px={4}>
            <div className={centerBorder}>
              <span className={redBox}>1</span>
            </div>
            <h3>Purchase Assessment Passwords</h3>
            <p>
              {' '}
              The Winslow Internet System enables you to purchase Profile Passwords electronically using a bank account
              or any credit/debit card. Discounts are offered when quantities of Passwords are purchased. When purchases
              are made, the Passwords are immediately placed in your "Available Passwords" account.
            </p>
          </Box>
          <Box width={[1, 1, 1 / 4]} px={4}>
            <div className={centerBorder}>
              <span className={redBox}>2</span>
            </div>
            <h3>Profile Password Activated for the Applicant</h3>
            <p>
              When you want to assess an applicant you go to the "Activate Assessment Password" function on your Winslow
              Website. Basic information is entered to identify the Participant. The PCS Position of the applicant is
              selected. The System sends a form to the printer with the participant's name, password and directions for
              completing the Profile. An email with this information can be sent to the applicant.
            </p>
          </Box>
          <Box width={[1, 1, 1 / 4]} px={4}>
            <div className={centerBorder}>
              <span className={redBox}>3</span>
            </div>
            <h3>Applicant Completes The Assessment Profile</h3>
            <p>
              To make certain the applicant is the person who completes the Profile, applicants should be monitored in
              the client's offices. The Profile takes less than one hour to complete and contains special control
              question to determine if the participant's answers are both accurate and objective. The System will not
              produce a Report when results are invalid. Approximately 30% of the applicants will have invalid results
              on their first assessment and must retake the Profile. This happens because some applicants give answers
              they think will provide desirable results. Others unintentionally exaggerate their answers because of a
              strong ego.
            </p>
          </Box>
          <Box width={[1, 1, 1 / 4]} px={4}>
            <div className={centerBorder}>
              <span className={redBox}>4</span>
            </div>
            <h3> Profiles Processed and Winslow Reports Prepared</h3>
            <p>
              Reports are available immediately to management when the applicant has accurate and objective assessment
              results. There are four versions of the Winslow Reports. The Executive Report contains a Personality
              Profile displaying the 24 trait scores and a Position Compatibility Form. When applicants are hired, the
              Manager's Report is given to the manager of the employee and the Participant's Report is given to your new
              employee. The PCS Summary Form is placed in the applicants file. (Please refer to all versions of the
              Winslow Reports).
            </p>
          </Box>
        </Flex>
        <Box px={4}>
          <h2 className={h2Class}> Decision to Hire an Applicant </h2>
          <p>
            {' '}
            The Position Compatibility Summary (PCS) Form presents the assessment data and behavioral requirements for
            each applicant on one color-coded page. The PCS Form compiles the results of a comprehensive, sophisticated
            assessment system into an easy to interpret document that simplifies the selection process. Management can
            then establish the cut-off" point on qualifying scores to determine whether applicants should be offered a
            position. The four major considerations are the "Participant's Net PCS Score," the number of "Concern Trait
            Scores," any negative "Trait Group Scores," and the "Key Characteristics" score.
          </p>
        </Box>

        <Box
          className={css`
            background-color: rgba(243, 244, 244, 1);
            padding: 30px 50px;
          `}
        >
          <h2 className={h2Class}>To schedule a demonstration of the Winslow Profiles System, click here.</h2>
          <Box
            p={2}
            className={css`
              text-align: center;
              padding: 10px 0 0 0;
            `}
          >
            <Link to="/sign-up" className={redButton}>
              Get Started Now
            </Link>
          </Box>
        </Box>
      </div>
    );
  }
}

export default withGA(WinslowInternetAssessment);



