import React from 'react';

const backdrop = props =>
  props.show ? (
    <div
      style={{
        width: '100%',
        height: '100%',
        position: 'fixed',
        zIndex: 99,
        left: 0,
        top: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.5)'
      }}
      onClick={props.clicked}
    />
  ) : null;

export default backdrop;



