import React, { Children, Component, cloneElement } from 'react';
// import Collapse from 'react-collapse';
import styled, { css } from 'react-emotion';
import arrowIcon from '../images/round-keyboard_arrow_down-24px.svg';
import SVGInline from 'react-svg-inline';

const Collapse = styled('div')(props => ({
  maxHeight: props.isOpened ? '700px' : '0',
  height: 'auto',
  overflow: 'hidden',
  transition: 'max-height .3s linear'
}));
class AccordionItem extends Component {
  render() {
    let { expanded, caption, onToggle, name, children } = this.props;

    return (
      <div
        className={css({
          margin: expanded ? '20px 0px' : '0px',
          border: '1px solid #ddd',
          borderBottom: expanded ? 'none' : '1px solid #ddd',
          boxShadow: expanded ? '0px 5px 5px #ddd' : 'none',
          transition: 'margin .3s linear',
          cursor: 'pointer'
        })}
      >
        <div
          className={css({
            display: 'flex',
            justifyContent: 'space-between',
            padding: '8px 10px 8px 20px'
          })}
          onClick={() => onToggle(name)}
        >
          <span>{caption}</span>
          <span
            className={css({
              svg: {
                transition: 'transform 0.3s linear',
                transform: expanded ? 'scale(1.5) rotate(-180deg)' : 'scale(1.5) rotate(0deg)',
                fill: '#db3a1b',
                marginBottom: '-5px'
              }
            })}
          >
            <SVGInline svg={arrowIcon} />
          </span>
        </div>
        <Collapse isOpened={expanded}>{children}</Collapse>
      </div>
    );
  }
}
export default AccordionItem;



