import React, { PureComponent } from 'react';
import styled, { css } from 'react-emotion';
import { Box } from 'grid-styled/emotion';
import checkIcon from '../images/baseline-check_circle_outline-24px.svg';
import SVGInline from 'react-svg-inline';

const CheckStyled = styled(SVGInline)(props => ({
  visibility: props.valid === 'true' ? 'visible' : 'hidden',
  paddingLeft: '5px',
  float: 'right',
  svg: {
    fill: 'rgba(0, 0, 0, 0.54)',
    height: '20px',
    width: '20px',
    alignSelf: 'flex-end',
    flexShrink: 0,
    marginRight: '0px',
    verticalAlign: 'bottom'
  }
}));

// const checkStyle = css`
//   svg {
//     fill: rgba(0, 0, 0, 0.54);
//     height: 20px;
//     width: 20px;
//     align-self: flex-start;
//     flex-shrink: 0
//     margin-right: 0px;
//   }
// `;

const inputBox = css`
  margin: 10px 10%;
  padding: 20px 0;
  position: relative;
`;

const InputStyled = styled('input')(props => ({
  padding: '10px',
  border: 'none',
  borderBottom: props.valid ? '2px solid black' : '1px solid #db3a1b',
  outline: 'none',
  color: props.valid ? 'black' : 'red',
  width: '100%'
}));

const LabelStyled = styled('label')(props => ({
  position: 'absolute',
  top: props.blur ? '0px' : '28px',
  left: props.blur ? '0px' : '10px',
  fontSize: props.blur ? '0.7rem' : '1rem',
  transition: 'all 0.3s ease-in-out 0s',
  width: '100%'
}));

export default class FormInput extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isDirty: false,
      isSubmitting: false,
      isValid: false,
      isBlurred: false
    };
    this.handleBlur = this.handleBlur.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
    // this.handleChange = this.handleChange.bind(this);
  }

  handleBlur = e => {
    if (this.props.value.length === 0) {
      this.setState({ isBlurred: false });
    }
  };

  handleFocus = e => {
    this.setState({ isBlurred: true });
  };

  render() {
    const { label, handleChange, value, valid } = this.props;
    const { isBlurred } = this.state;
    return (
      <Box className={inputBox}>
        <LabelStyled blur={isBlurred} htmlFor={label}>
          {label.replace(/^\w/, c => c.toUpperCase())}
          <CheckStyled svg={checkIcon} valid={`${valid}`} />
        </LabelStyled>
        <InputStyled
          valid={valid}
          id={label}
          label={label}
          value={value}
          onChange={handleChange}
          onBlur={this.handleBlur}
          onFocus={this.handleFocus}
        />
      </Box>
    );
  }
}



